.linked-list {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 50px;
  }

  .node {
    padding: 10px;
    background-color: #f2f2f2;
    border: 1px solid #ccc;
    border-radius: 10px;
    text-align: center;
    margin: 0 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .arrow {
    margin: 0 10px;
    font-size: 24px;
    font-weight: bold;
  }

  .node img {
    width: 50px;
    height: 50px;
  }

  .node .label {
    margin-top: 10px;
    font-weight: bold;
  }