.container {
    max-width: 600px;
    margin: 50px auto;
    background-color: #fff;
    padding: 30px;
    border-radius: 10px;

  }

  h2 {
    text-align: center;
    color: #333;
  }

  form {
    display: flex;
    flex-direction: column;
  }

  label {
    margin: 10px 0 5px;
    color: #555;
  }

  input, textarea {
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 24px;
    font-size: 16px;
    width: 100%;
    box-sizing: border-box;
  }

  textarea {
    height: 100px;
    resize: none;
  }

  button {
    margin-top: 20px;
    padding: 10px;
    background-color:rgb(21, 21, 21);
    color: white;
    border: none;
    width: max-content;
    border-radius: 14px;
      
  }


  .footer {
    text-align: center;
    margin-top: 30px;
    color: #777;
  }